import React, { useContext, useEffect } from "react";
import { DataContext } from "contexts/GlobalContext";
import Header from "components/Header/Header";
import { StreamingIndex } from "pages/StreamingPage/StreamingIndex";
import PhoneIndex from "pages/phoneIndex/PhoneIndex";
import "./App.css";
import Loader from "components/Loader/Loader";

const MainContent = () => {
  const { linkType } = useContext(DataContext);

  useEffect(() => {
    console.log("Link type:", linkType);
  }, [linkType]);

  return (
    <>
      {linkType === "Video call" && <StreamingIndex />}
      {linkType === "Phone call" && <PhoneIndex />}
      {linkType !== "Video call" && linkType !== "Phone call" && <Loader />}
    </>
  );
};

export default MainContent;
