// pages/StreamingIndex.js
import React, { useContext, useEffect, useState } from "react";
import { useScribe } from "../../contexts/ScribeContext";
import "./StreamingIndex.scss";
import { DataContext } from "contexts/GlobalContext";

export const StreamingIndex = () => {
  const {
    isRecording,
    isPaused,
    transcriptText,
    handleStart,
    handleStop,
    handlePause,
    handleResume,
  } = useScribe();
  const { demographicId, linkType } = useContext(DataContext);

  const [id, setId] = useState("");

  useEffect(() => {
    setId(demographicId);
  }, [demographicId]);

  return (
    <div className="streaming-index">
      <div className="transcription-container">
        <h3>Live Transcription:</h3>
        <p className="transcript-text">{transcriptText}</p>
      </div>
      <p>{demographicId || "No demographic ID received."}</p>
      <p>{linkType || "No Link."}</p>
      <div className="button-container">
        {!isRecording ? (
          <button onClick={handleStart} className="myextBtn">
            Turn On Mic
          </button>
        ) : isPaused ? (
          <button onClick={handleResume} className="myextBtn">
            Resume
          </button>
        ) : (
          <>
            <button onClick={handlePause} className="myextBtn">
              Pause
            </button>
            <button onClick={handleStop} className="myextBtn">
              Turn Off Mic
            </button>
          </>
        )}
      </div>
    </div>
  );
};
