import React, { createContext, useState, useEffect } from "react";

// Create context
const DataContext = createContext();

// Create provider component
const DataProvider = ({ children }) => {
  const [demographicId, setDemographicId] = useState("");
  const [linkType, setLinkType] = useState("");

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;

      if (event.data.type === "UPDATE_DEMOGRAPHIC_INFO") {
        const id = event.data.demographicId;
        const receivedLinkType = event.data.linkType;
        console.log("Received Demographic ID:", id);
        console.log("Received Link Type:", receivedLinkType);

        setDemographicId(id);
        setLinkType(receivedLinkType);
      }
    };

    window.addEventListener("message", handleMessage);

    return () => {
      window.removeEventListener("message", handleMessage);
    };
  }, []);

  return (
    <DataContext.Provider value={{ demographicId, linkType }}>
      {children}
    </DataContext.Provider>
  );
};

export { DataContext, DataProvider };
